.detail{
      background-color: white;
      margin-bottom: 70px;
      font-weight: 300;
      border-radius: 5px;
      color: #adaeb9;
      text-decoration: none;
      color: inherit;
      box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
      max-width:50%;
      position:absolute;
      left:50%;
      top:50%;
      transform: translate(-50%, -50%);
}